import {MODE_LIST, MODE_REPLACE} from './types'
import regexps from './regexps.json'

export const defaultResolve = {
  [MODE_LIST]: '$&\\n',
  [MODE_REPLACE]: '[$&]',
}

export const defaultRegexp = {
  source: '',
  flags: 'g',
  input: '',
  resolve: defaultResolve[MODE_LIST],
  mode: MODE_LIST,
}

export const demoRegexp = Object.assign(
  {},
  defaultRegexp,
  Object.values(regexps)[0],
)

export const flags = [{
  value: 'g',
  title: '',
}, {
  value: 'i',
  title: '',
}, {
  value: 'm',
  title: '',
}]
const check = function (flag) {
  try {
    RegExp('', flag)
    return true
  } catch (e) {
    return false
  }
}
if (check('u')) {
  flags.push({
    value: 'u',
    title: 'Unicode',
  })
}
if (check('y')) {
  flags.push({
    value: 'y',
    title: '',
  })
}

export const modes = [{
  value: MODE_LIST,
  title: '',
}, {
  value: MODE_REPLACE,
  title: '',
}]

export const MIN_DELAY = 100
export const MAX_DELAY = 600



